import Layout from "../../components/Layout"
import { useRouter } from 'next/router'
import posts from '../../static/topic.json'

const OtherTopic = (props) => {
    const otherTopics = props.posts.filter(post => post.linkName != props.currLinkName);
    
    otherTopics.map(topic => {
    <h1>This is topic {topic.name}</h1>
    })
}

const Post = () => {
    const router = useRouter();
    const currLinkName = router.query.postName;
    
    // how do I get the name of this link name?
    return (
        <Layout>
            <OtherTopic posts={posts} currLinkName={currLinkName}/>
        </Layout>
    );
}
export default Post;